import "./Dropdown.scss";
import React, { useState } from "react";

export interface DropdownProps {
  name: string;
  placeholder?: string;
}

const DropDown = React.forwardRef<any, React.HTMLProps<DropdownProps>>((props, ref) => {
  const [value, setValue] = useState("");
  const onChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    if (e.currentTarget) {
      let val = e.currentTarget.value;
      if (val) {
        setValue(e.currentTarget.options[e.currentTarget.selectedIndex].text);
      } else {
        setValue("");
      }
    }
  };
  return (
    <div className={`dropdown ${value ? "active" : ""}`}>
      <input type="text" value={value} readOnly />
      <select onChange={e => onChange(e)} ref={ref} name={props.name}>
        {props.children}
      </select>
      <span className="placeholder">{props.placeholder}</span>
    </div>
  );
});

export default DropDown;
