import axios from "axios";
import { ButtonDataType } from "components/base/strength/Strength";
import DropDown from "components/common/dropdown/Dropdown";
import React, { createRef } from "react";
import { useForm } from "react-hook-form";
import { useLocalize } from "store/actions/localizeAction";
import Swal from "sweetalert2";
import { COUNTRIES, RECAPTCHA_SITEKEY } from "utils";
import { recaptcha3 } from "utils/recaptcha/ReCaptchaV3";
import "./FormContact.scss";

export interface FormContactProps {
  showed?: boolean;
  setShow?: Function;
}
type FormData = {
  name: string;
  email: string;
  country: string;
  message: string;
  company: string;
  phone: string;
  token: string;
  checkbox: any;
};
export interface FormContactDataType {
  label: {
    title: string;
    intro: string;
    fullname: string;
    email: string;
    company: string;
    number: string;
    country: string;
    message: string;
    gameloftTouPp: string;
    recaptchaTouPp: string;
  };
  inputText: {
    [key: string]: {
      placeholder: string;
    };
  };
  textArea: {
    [key: string]: {
      placeholder: string;
    };
  };
  button: ButtonDataType;
  errorMessage: {
    [key: string]: string;
  };
  popupMessages: {
    [key: string]: {
      title: string;
      body: string;
    };
  };
}

const FormContact: React.FC<FormContactProps> = (props) => {
  const { register, handleSubmit, errors } = useForm<FormData>({
    mode: "onBlur",
  });
  const data: FormContactDataType = useLocalize<any>("intro")?.formContact;
  const renderHTML = require("react-render-html");
  const formRef = createRef<HTMLFormElement>();

  const onSubmit = async (dataSend: FormData) => {
    const captcha = await recaptcha3.execute(RECAPTCHA_SITEKEY, "ContactForm");
    dataSend = { ...dataSend, token: captcha };
    formRef.current?.reset();
    // props.setShow(false);

    axios
      .post(`${window.APP_API_URL}/api/user/contact`, dataSend)
      .then(function (response) {
        setTimeout(() => {
          Swal.fire(
            data?.popupMessages.success.title,
            data?.popupMessages.success.body,
            "success"
          );
        }, 500);
      })
      .catch(function (error) {
        setTimeout(() => {
          Swal.fire(
            data?.popupMessages.fail.title,
            data?.popupMessages.fail.body,
            "error"
          );
        }, 500);
      });
  };

  const isValid = () => Object.keys(errors).length !== 0;

  return (
    <section className={`com-form-contact animated faster visible`}>
      <h2 className="title">{data?.label.title}</h2>
      <h4>{data?.label.intro}</h4>
      <form ref={formRef} onSubmit={handleSubmit(onSubmit)}>
        <div className="item">
          <span className="label">{data?.label.fullname}</span>
          {errors.name && (
            <span className="error animated fadeIn faster">
              {data?.errorMessage.nameRequired}
            </span>
          )}
          <input
            type="text"
            name="name"
            placeholder={data?.inputText.fullname.placeholder}
            ref={register({ required: true })}
          />
        </div>

        <div className="item">
          <span className="label">{data?.label.email}</span>
          {errors.email && errors.email.type === "required" && (
            <span className="error animated fadeIn faster">
              {data?.errorMessage.emailRequired}
            </span>
          )}
          {errors.email && errors.email.type === "pattern" && (
            <span className="error animated fadeIn faster">
              {data?.errorMessage.emailInvalid}
            </span>
          )}
          <input
            type="text"
            name="email"
            placeholder={data?.inputText.email.placeholder}
            ref={register({
              required: true,
              pattern: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
            })}
          />
        </div>

        <div className="item">
          <span className="label">{data?.label.company}</span>
          {errors.company && (
            <span className="error animated fadeIn faster">
              {data?.errorMessage.companyRequired}
            </span>
          )}
          <input
            type="text"
            name="company"
            placeholder={data?.inputText.company.placeholder}
            ref={register({ required: true })}
          />
        </div>

        <div className="item">
          <span className="label">{data?.label.number}</span>
          <input
            type="number"
            name="phone"
            placeholder={data?.inputText.number.placeholder}
            ref={register()}
          />
        </div>

        <div className="item">
          <span className="label">{data?.label.country}</span>
          <DropDown
            placeholder="France"
            name="country"
            ref={register({ required: true })}
          >
            {COUNTRIES.map((country, index) => {
              return (
                <option key={country.value} value={country.value}>
                  {renderHTML(country.name)}
                </option>
              );
            })}
          </DropDown>
        </div>

        <div className="item mess">
          <span className="label">{data?.label.message}</span>
          {errors.message && (
            <span className="error animated fadeIn faster">
              {data?.errorMessage.messageRequired}
            </span>
          )}
          <textarea
            rows={4}
            name="message"
            placeholder={data?.textArea.message.placeholder}
            ref={register({ required: true })}
          />
        </div>
        {/* Checkbox */}
        <div className="item checkbox">
          <input
            type="checkbox"
            id="checkbox"
            name="checkbox"
            ref={register({ required: true })}
          />
          <label htmlFor="checkbox">
            {data?.label.gameloftTouPp && renderHTML(data.label.gameloftTouPp)}
          </label>
          <div className="recaptcha mg-top0">
            {data?.label.recaptchaTouPp &&
              renderHTML(data.label.recaptchaTouPp)}
          </div>
          {errors.checkbox && (
            <div className="errCheck animated fadeIn faster">
              {" "}
              {data?.errorMessage.confirmGameloftTouPpRequired}
            </div>
          )}
        </div>

        <button className="btn-submit" disabled={isValid()}>
          {data?.button.sendEmail.text}
        </button>
      </form>
    </section>
  );
};

export default FormContact;
