import LazyImage from "libs/lazy-load/LazyImage";
import React, { useEffect } from "react";
import { useLocalize } from "store/actions/localizeAction";
import "./Introduce.scss";
const WOW = require("wowjs");

export interface IntroduceDataType {
  label: {
    intro: string;
    quote: string;
    scroll: string;
  };
  visual: {
    mimeType: string;
    url: string;
  };
  logo: {
    mimeType: string;
    url: string;
  };
}
export interface IntroduceProps {
  scrollable?: any;
  ishomepage?: any;
}

const Introduce: React.FC<IntroduceProps> = (props) => {
  useEffect(() => {
    const wow = new WOW.WOW({
      live: false,
    });
    wow.init();
  }, []);
  const renderHTML = require("react-render-html");
  const data = useLocalize<IntroduceDataType>("intro");

  const renderNotHomepage = () => {
    if (data.visual.mimeType.includes("video")) {
      return <video src={data.visual.url} loop autoPlay={true} muted></video>;
    } else {
      return <LazyImage className="img-bg" src={data.visual.url} alt="" />;
    }
  };

  const renderHomepage = () => {
    if (data.visual.mimeType.includes("video")) {
      return <video src={data.visual.url} loop autoPlay={true} muted></video>;
    } else {
      return <div className="div-bg" >
        <LazyImage className="lazaImg-abs" src={data.visual.url}></LazyImage>
      </div>;
    }
  };

  return (
    <section className="com-introduce " id="introduce" {...props}>
      <div className="wrapper-video">
        {data?.visual && (props.ishomepage === "true" ? renderHomepage() : renderNotHomepage())}
        {/* {props.ishomepage && data?.logo && <img className="logo" src={data.logo.url} alt="logoIntro" />} */}
      </div>

      <div className="wrapper-content">
        {props.ishomepage === "true" && data?.logo && <LazyImage src={data.logo.url} alt="logo" />}

        <div className={`${props.ishomepage === "true" ? "" : "top"} block-content`}>
          <div className="first-content ">
            <h2 className="title wow fadeInUp">{data?.label.intro}</h2>
            <div className="content wow fadeInUp" data-wow-delay="0.2s">
              {data?.label.quote && renderHTML(data.label.quote)}
            </div>
          </div>
        </div>
      </div>

      {props.scrollable === "true" && (
        <div className="block-scroll1">
          <div className="hr-scroll"></div>
          <div className="bounceScroll">{data?.label.scroll}</div>
        </div>
      )}
    </section>
  );
};

export default Introduce;
